
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
	 data () {
	    return {
	          subject:null,//电影条目
	          loadingText:"loading",//加载文字
	          loading: true, //是否加载
	    }
	 },
	 components: {
	      Swiper,Badge,Flexbox, FlexboxItem,Loading
	 },
	 mounted: function() {
	 	 this.getSubject();
     },
     methods: {
     	   async getSubject(){
     	   	      let data = await this.$http.get(`/v2/movie/subject/${this.$route.params.id}`);
     	   	      console.log(data)
	               if (data.status == 200) {
		              	this.subject=data.data;
		                this.loading=false;
	               }
	        }
     }
}



