
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
	 data () {
	    return {
	          movies:[],
	          loadingText:"loading",
	          loading: true, //是否加载
              pageSize: 5, //每页条数
              currentPage: 1,//当前页
              more:true//更多
	    }
	 },
	 components: {
	      Swiper,Badge,Loading
	 },
	 mounted: function() {
	 	 window.addEventListener('scroll', this.handleScroll);
	 	 this.getMovies();
     },
     methods: {
     	   async getMovies(){
     	   	      let start=(this.currentPage-1)*this.pageSize;
     	   	      let data = await this.$http.get(`/v2/movie/${this.$route.params.type}?start=${start}&count=${this.pageSize}`);
	               if (data.status == 200) {
		              	if(data.data.subjects.length >= 0){
		             	   this.movies=this.movies.concat(data.data);
		              	}else{
	                       this.more=false;
		                }
		                this.loading=false;
	                }
	        },
	        handleScroll (e) {
			     if(document.body.scrollTop >= document.body.scrollHeight - window.screen.height && !this.loading) {
						this.loading=true;
						this.currentPage++;
						this.getMovies();
				     }
		    }
     }
}



